.register__input {
  color: #cccccc;
  background-color: #000000;
  border: none;
  border-bottom: 2px solid rgba(204, 204, 204, 1);
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  margin: 0 0 30px;
  padding-bottom: 13px;
  outline: none;
}

@media (max-width: 640px) {
  .register__input {
    max-width: 238px;
  }
}

.register__input:last-of-type {
  margin: 0;
}
